import { jsPDF } from "jspdf";
import "jspdf-autotable";

function create(items) {
  // Default export is a4 paper, portrait, using millimeters for units
  const doc = new jsPDF();

  doc.text("DICTAMEN DE LAS INSTALACIONES ELECTRICAS 2022 PRUEBA", 15, 20);

  doc.autoTable({
    startY: 30,
    head: [["SECCIÓN", "DESCRIPCIÓN", "CUMPLE", "NO CUMPLE"]],
    body: items,
  });
  //window.open(doc.output("bloburl"));
  doc.save("DICTAMEN_PRUEBA.pdf");
}

export default {
  create,
};
