<template>
  <div>
    <div v-for="(item, i) in items" :key="i">
      <item-nom
        :nom="item.nom"
        :description="item.description"
        :value="item.value"
        @setOption="(value) => (item.value = value)"
      />
    </div>
    <template>
      <div class="text-center">
        <!--  loading -->
        <v-btn
          :loading="loading"
          class="ma-2"
          outlined
          large
          fab
          color="blue accent-4"
          @click="generatePDF"
        >
          <v-icon> mdi-cloud-download</v-icon>
        </v-btn>
      </div>
    </template>
  </div>
</template>

<script>
import report from "@/pdf/report-example.js";
import ItemNom from "@/components/Item.vue";
export default {
  name: "IndexView",
  components: {
    ItemNom,
  },
  data() {
    return {
      loading: false,
      drawer: null,
      items: [
        {
          nom: "110-16",
          description: "Señales de advertencia contra arco eléctrico. ",
          value: 0,
          risk: {
            0: "No aplica",
            1: "No aplica",
            2: "Alto",
          },
        },
        {
          nom: "110-22",
          description: "Identificación de los medios de desconexión. ",
          value: 0,
        },
        {
          nom: "408-4",
          description:
            "Identificación de campo requerido. a) Directorio del circuito. ",
          value: 0,
        },
        {
          nom: "110-26",
          description: "Espacio de trabajo alrededor de equipo eléctrico. ",
          value: 0,
        },
        {
          nom: "408-17",
          description:
            "Ubicación con respecto a material fácilmente inflamable. ",
          value: 0,
        },
      ],
    };
  },
  methods: {
    generatePDF() {
      this.loading = true;

      let format = [];
      let v1 = 0;
      let v2 = 0;
      this.items.forEach((i) => {
        //console.log(i);

        v1 = 0;
        v2 = 0;

        if (i.value == 1) {
          //cumple
          v1 = 1;
        }
        if (i.value == 2) {
          //no cumple
          v2 = 1;
        }

        format.push([i.nom, i.description, v1, v2]);
      });
      //console.log(format);

      setTimeout(() => (report.create(format), (this.loading = false)), 1000);
    },
  },
};
</script>
