<template>
  <v-card
    elevation="1"
    :loading="loading"
    class="mx-auto my-12"
    max-width="374"
  >
    <v-card-title> {{ nom }}</v-card-title>

    <v-card-text>
      <strong>Descripción:</strong>
      <div>{{ description }}</div>
    </v-card-text>

    <v-divider class="mx-4"></v-divider>

    <v-card-text>
      <v-chip-group
        v-model="selection"
        active-class="blue accent-4 white--text"
        column
        @change="cambio"
      >
        <v-chip value="1">Cumple</v-chip>

        <v-chip value="2">No Cumple</v-chip>

        <v-chip value="0">No aplica</v-chip>
      </v-chip-group>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "ItemBasic",
  props: {
    nom: {
      type: String,
      default: "Norma",
    },
    description: {
      type: String,
      default: "Descripción",
    },
    value: {
      type: Number,
      default: 0,
    },
  },
  data: () => ({
    loading: false,
    selection: null,
  }),

  methods: {
    cambio() {
      //console.log(this.selection);
      this.$emit("setOption", parseInt(this.selection, 10));
    },
    /*   reserve() {
      this.loading = true;

      setTimeout(() => (this.loading = false), 2000);
    }, */
  },
};
</script>
